.wrapper-loader {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.wrapper-loader.is-hidden {
  display: none;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px dotted #fff;
  border-radius: 50%;
  animation: rotation 2s linear infinite;
  display: inline-block;
  position: relative;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.container {
  width: 1000px;
  margin-left: auto;
  margin-right: auto;
}

.cat-info {
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  margin-top: 30px;
  display: grid;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

